import React, { useState } from "react";
import "antd/dist/antd.css";
import "./login.css";
import { Link, withRouter, Redirect } from "react-router-dom";
import { Row, Col, Form, Icon, Input, Button, Checkbox } from "antd";
import logo from "../../assets/Icon-Prototype-Screens (3)/icons/logo.png";
import { login } from "../../redux/Thunk/authThunk";
import { connect } from "react-redux";
import { Formik } from "formik";

import PasswordForgot from "../forgetPassword/forgetPassword"
function Login(props) {
  const [formData, setFormData] = useState({
    email: "",
    password: "",
    remember:false,
    animationToggle:false,
    firstTime:0
  });
  const { email, password, remember } = formData;
  const handleChange = e => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };
  const { getFieldDecorator } = props.form;
  function onChange(e) {
    setFormData({ ...formData, [e.target.name]: e.target.checked });
    console.log(`checked = ${e.target.checked}`);
  }
  const handleSubmit = e => {
    console.log("check form data", formData);
    e.preventDefault();
    props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log("Received values of form: ", values);
        console.log(values)
        props.loginUser(formData);
      }
    });
  };

  const togleAnimationFunc=()=>{
    const {animationToggle,firstTime} = formData
    if(animationToggle){
      setFormData({...formData ,animationToggle:false ,firstTime:firstTime+1})
    }else{
      setFormData({...formData ,animationToggle:true,firstTime:firstTime+1 })
    }
  }

  return (
    <>
        <div className="page-container login page">
          <div className="Login-Container">
            <Row type="flex" justify="center">
              <Col>
                <Row className="innnerContainer">
                  <Col span={24} className="logoStyle">
                    <img className="img" src={logo} />
                  </Col>

                  <Col span={24}>
                    <p>Wellcome back! Please login to your account</p>
                  </Col>
                  <br />
                  <Col span={24}>
                    <div className="">
                      <Row type="flex" justify="center">
                    
                            <Form onSubmit={handleSubmit}>
                              <Col span={24}>
                                {" "}

                                {/* <Formik
                          initialValues={{ email: "" }}
                          validate={values => {
                            const errors = {};
                            if (!values.email) {
                              errors.email = "Required";
                            } else if (
                              !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(
                                values.email
                              )
                            ) {
                              errors.email = "Invalid email address";
                            }

                            return errors;
                          }}
                          onSubmit={(values, { setSubmitting }) => {
                            setTimeout(() => {
                              alert(JSON.stringify(values, null, 2));
                              setSubmitting(false);
                            }, 400);
                          }}
                        >
                          {({
                            values,
                            errors,
                            touched,
                            handleChange,
                            handleBlur,
                            handleSubmit,
                            isSubmitting
                          }) => ( */}
                               <div><Input
                                  // className="inputStyle"
                                  name="email"
                                  value={formData.email}
                                  onChange={handleChange}
                                  // value={values.email}
                                  // onBlur={handleBlur}
                                  placeholder="Email"
                                  className="inputStyle"
                                  prefix={
                                    <Icon
                                      type="mail"
                                      style={{
                                        color: "white",
                                        fontWeight: "bold",
                                        textIndent: "10px",
                                        fontSize: "15px"
                                      }}
                                    />
                                  }
                                />
                                {/* {errors.email} */}
                                </div> 
                           {/* )} */}
                           {/* </Formik> */}
                              </Col>
                              <br />
                              <br />
                              <Col span={24}>
                              {" "}
                    <Input.Password
                      placeholder="Password"
                      // className="inputStyle"
                      name="password"
                      value={formData.password}
                      onChange={e => handleChange(e)}
                      prefix={
                        <Icon
                          type="lock"
                          style={{
                            color: "white",
                            fontWeight: "bold",
                            textIndent: "10px",
                            fontSize: "15px"
                          }}
                        />
                      }
                    />
                  </Col>{" "}
                  <br />
                  <br />
                  <Col span={22}>
                    <span className="handelCheckBoxes">
              
                      <Checkbox onChange={onChange} name="remember" value={remember} >Remember</Checkbox>

                      <span onClick={togleAnimationFunc} >Forget password ?</span>
                    </span>
                  </Col>
                  <br />
                  {/* <Row> */}
                    <Col span={11}>
                      <Button onClick={handleSubmit} id="handelButtonStyle" disabled={props.UserState.loading}>
                        Login
                        {/* <Link to="/home"> Login</Link> */}
                      </Button>
                    </Col>
                    <Col span={11}>
                      <Button
                        onClick={() => props.history.push("/")}
                        id="handelButtonRegisterStyle"
                      >
                        Register
                      </Button>
                    </Col>
                  {/* </Row> */}
                </Form>

               </Row>

                <PasswordForgot  firstTime={formData.firstTime} animationHandle ={formData.animationToggle} animationHandler={togleAnimationFunc} />
              </div>
            </Col>
          </Row>
          </Col>
        </Row>
     
      </div>
    </div>
    </>
  );
}
const WrappedRegistrationForm = Form.create({ name: "register" })(Login);

const mapDispatchToProps = (Dispatch,props) => ({
  loginUser: state => Dispatch(login(state,props))
});

const mapStateToProps = state => ({
  UserState: state.userReducer
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(WrappedRegistrationForm));
