import React, { useState } from "react";
import "antd/dist/antd.css";
import "./signup.css";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { SignUp } from "../../redux/Thunk/authThunk";
import { Row, Col, Form, Icon, Input, Button, Checkbox } from "antd";
import logo from "../../assets/Icon-Prototype-Screens (3)/icons/logo.png";
import { withRouter, Redirect } from "react-router-dom";
import { Formik } from "formik";
function Signup(props) {
  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    email: "",
    password: "",
    phone: "",
    terms: false,
    privacy: false
  });
  const {
    firstName,
    lastName,
    email,
    password,
    phone,
    terms,
    privacy,
    dis
  } = formData;
  const handleChange = e => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };
  const { getFieldDecorator } = props.form;
  function onChange(e) {
    setFormData({ ...formData, [e.target.name]: e.target.checked });
    console.log(`checked = ${e.target.checked}`);
  }
  const handleSubmit = e => {
    console.log(formData);
    e.preventDefault();

    props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log("Received formData of form: ", values);
        props.SignUp(formData);
      }
    });
  };

  return (
    <>
       <div className="page-container signup page">
          <div className="Signup-Container">
            <Row type="flex" justify="center">
              <Col span={10}>
                <Row className="innnerContainer" type="flex" justify="center">
                  <Col span={24} className="logoStyle">
                    <img className="img" src={logo} />
                  </Col>
                  <Col span={24}>
                    <p>Please complete to create your account</p>
                  </Col>
                  <Col span={24}>
                    <div>
                   
                          <Form onSubmit={handleSubmit}>
                            <Row type="flex" justify="center">
                              <Col span={10}>
                                <Input
                                  placeholder="First name"
                                  name="firstName"
                                  value={firstName}
                                  // onChange={e => handleChange(e)}
                                  onChange={handleChange}
                                  value={formData.firstName}
                                  prefix={
                                    <Icon
                                      type="user"
                                      style={{
                                        color: "white",
                                        fontWeight: "bold",
                                        textIndent: "10px",
                                        fontSize: "15px"
                                      }}
                                    />
                                  }
                                />
                               
                              </Col>
                              <Col span={10}>
                                {" "}
                                <Input
                                  placeholder="Last name"
                                  name="lastName"
                                  value={lastName}
                                  // onChange={e => handleChange(e)}
                                  onChange={handleChange}
                                  value={formData.lastName}
                                  prefix={
                                    <Icon
                                      type="lock"
                                      style={{
                                        color: "white",
                                        fontWeight: "bold",
                                        textIndent: "10px",
                                        fontSize: "15px"
                                      }}
                                    />
                                  }
                                />
                               
                              </Col>
                              <Col span={20}>
                                {" "}
                                <Input
                                  placeholder="Email"
                                  name="email"
                                  value={email}
                                  // onChange={e => handleChange(e)}
                                  onChange={handleChange}
                                  value={formData.email}
                          
                                  prefix={
                                    <Icon
                                      type="mail"
                                      style={{
                                        color: "white",
                                        fontWeight: "bold",
                                        textIndent: "10px",
                                        fontSize: "15px"
                                      }}
                                    />
                                  }
                                />
                              </Col>
                              <Col span={20}>
                                {" "}
                                <Input
                                  placeholder="Password"
                                  name="password"
                                  value={password}
                                  onChange={e => handleChange(e)}
                                  value={formData.password}
                                  prefix={
                                    <Icon
                                      type="lock"
                                      style={{
                                        color: "white",
                                        fontWeight: "bold",
                                        textIndent: "10px",
                                        fontSize: "15px"
                                      }}
                                    />
                                  }
                                />
                              
                              </Col>{" "}
                              <br />
                              <Col span={20}>
                                {" "}
                                <Input
                                  placeholder="Phone"
                                  name="phone"
                                  value={phone}
                                  onChange={e => handleChange(e)}
                                  value={formData.phone}
                                  prefix={
                                    <Icon
                                      type="phone"
                                      style={{
                                        color: "white",
                                        fontWeight: "bold",
                                        textIndent: "10px",
                                        fontSize: "15px"
                                      }}
                                    />
                                  }
                                />
                                
                              </Col>
                              <Col span={20}>
                                <span className="handelCheckBoxes">
                                  <Checkbox
                                    onChange={onChange}
                                    value={terms}
                                    name="terms"
                                  >
                                    Term of Business and Privacy Policy
                                  </Checkbox>
                                </span>
                              </Col>
                              <Col span={20}>
                                <span className="handelCheckBoxes">
                                  <Checkbox
                                    onChange={onChange}
                                    name="privacy"
                                    value={privacy}
                                  >
                                    Have You Read and agree with our Terms or
                                    <br />
                                    business and Privacy Polices?
                                  </Checkbox>
                                </span>
                              </Col>
                              <Col span={20}>
                                <Button
                                  onClick={handleSubmit}
                                  className="handelButtonStyle"
                                  disabled={
                                    terms && privacy && !props.UserState.loading
                                      ? false
                                      : true
                                  }
                                >
                                  {terms}Register
                                </Button>
                              </Col>
                            </Row>
                          </Form>
                       
                      <Col span={20}>
                        Already have a account?{" "}
                        <Link to="/login">Login Here</Link>
                      </Col>
                    </div>
                  </Col>
                </Row>
              </Col>
            </Row>
          </div>
        </div>
    </>
  );
}
const WrappedRegistrationForm = Form.create({ name: "register" })(Signup);
const mapDispatchToProps = (Dispatch,props) => ({
  SignUp: state => Dispatch(SignUp(state,props))
});

const mapStateToProps = state => ({
  UserState: state.userReducer
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(WrappedRegistrationForm);
