import React from "react"
import Chat from "../../chat/chat";
import {Col} from "antd"
import RightBar from "../rightbar/rightbar";

function Feed() {
  return (
    <>
      <Col lg={18}>
        <Chat />
      </Col>
      <Col className="gutter-row" lg={6}>
        <div className="gutter-box rightbar-container">
          <RightBar />
        </div>
      </Col>
    </>
  );
}
export default Feed;
