import React from "react";
import { Row, Col, Collapse } from "antd";
import "./home.css";
import Chat from "../chat/chat";
import LeftBar from "./leftbar/leftbar";
import SearchBar from "./searchbar/searchbar";
import RightBar from "./rightbar/rightbar";
import UserSettings from "../userSettings/userSettings";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import {Route, Switch} from "react-router-dom"
import Feed from "./feed/feed";
// import ChatBox from "./chatBox"

const { Panel } = Collapse;

function Home(props) {
  return (
    <>
    {
// !props.UserState.user.isVerified&&
// <Redirect to="/verifymail" />||
    
    <div>
      <Row gutter={0}>
        <Col className="gutter-row" lg={5}>
          <div className="gutter-box leftbar-container">
            <LeftBar />
          </div>
        </Col>
        <Col className="gutter-row" lg={19}>
          <div className="gutter-box">
            <Row>
              <Col lg={24}>
                <SearchBar/>
              </Col>
              <Switch>
                <Route path="/home/settings" component={UserSettings} />
                <Route path="/home" component={Feed} />
              </Switch>
              
            </Row>
          </div>
        </Col>
      </Row>
    </div>
    }
    </>
  );
}

const mapStateToProps=state=>({
  UserState:state.userReducer
})

export default connect(mapStateToProps)(Home);
